<template>
  <form @submit.prevent="handleSubmit">
    <div class="mb-2 text-center">
      <h4 class="mb-3">Transactions</h4>
      <div v-if="adding">
        <UiButton class="width-full mb-2">
          <input
            v-model="newEntry.to"
            class="input width-full text-center"
            placeholder="Target address"
            required
          />
        </UiButton>
        <UiButton class="width-full mb-2">
          <input
            v-model="newEntry.value"
            class="input width-full text-center"
            placeholder="Value"
            required
          />
        </UiButton>
        <UiButton class="width-full mb-2">
          <input
            v-model="newEntry.data"
            class="input width-full text-center"
            placeholder="Data"
            required
          />
        </UiButton>
        <UiButton class="width-full mb-2">
          <input
            v-model="newEntry.operation"
            class="input width-full text-center"
            placeholder="Operation"
            required
          />
        </UiButton>
        <UiButton @click="adding = false" class="mb-2">
          Back
        </UiButton>
        <UiButton :disabled="!isValid" @click="addTx" class="button--submit">
          Add
        </UiButton>
      </div>
      <div v-else>
        <div
          v-for="(tx, i) in input.txs"
          :key="i"
          class="mb-3 p-4 border rounded-2 text-white text-center"
        >
          <b
            v-text="
              `${input.txs[i].operation === '0' ? 'Call' : 'Delegate Call'} to`
            "
          /><br />
          <jazzicon
            :address="input.txs[i].to"
            :diameter="parseInt(size) || 22"
            class="d-inline-block v-align-middle line-height-0"
          />
          <b v-text="_shorten(input.txs[i].to)" /><br />
          with <font v-text="input.txs[i].value" /> WEI<br />
          <font v-text="_shorten(input.txs[i].data, 10)" />
          <UiButton v-if="input" @click="removeTx(i)" class="width-full mb-2">
            Remove
          </UiButton>
        </div>
        <UiButton @click="adding = true" class="width-full mb-2">
          Add
        </UiButton>
        <UiButton @click="handleSubmit" class="button--submit width-full">
          Confirm
        </UiButton>
      </div>
    </div>
  </form>
</template>

<script>
const isHexString = value => {
  return /^0x[0-9A-Fa-f]*$/i.test(value);
};
export default {
  props: ['modelValue', 'proposal', 'network'],
  emits: ['update:modelValue', 'close'],
  data() {
    return {
      input: false,
      adding: false,
      newEntry: {}
    };
  },
  computed: {
    isValid() {
      // TODO: use plugin
      return (
        this.newEntry.value &&
        (parseInt(this.newEntry.value) || parseInt(this.newEntry.value, 16)) >=
          0 &&
        this.newEntry.to?.length == 42 &&
        isHexString(this.newEntry.to) &&
        (!this.newEntry.data ||
          (this.newEntry.data.length % 2 == 0 &&
            isHexString(this.newEntry.data))) &&
        this.newEntry.operation in ['0', '1']
      );
    }
  },
  mounted() {
    if (this.modelValue) return (this.input = this.modelValue);
  },
  methods: {
    addTx() {
      if (!this.input) this.input = { txs: [] };
      this.input.txs.push({
        to: this.newEntry.to,
        value: this.newEntry.value,
        data: this.newEntry.data,
        operation: this.newEntry.operation
      });
      this.newEntry = {};
      this.adding = false;
    },
    removeTx(index) {
      if (!this.input || !this.input.txs || this.input.txs.length <= index)
        return;
      this.input.txs.splice(index, 1);
      if (this.input.txs.length == 0) {
        this.input = false;
      }
    },
    addAction() {
      if (!this.input) this.input = {};
      this.input = {
        txs: []
      };
    },
    removeAction() {
      this.input = false;
    },
    handleSubmit() {
      this.$emit('update:modelValue', this.input);
      this.$emit('close');
    },
    getChoices() {
      return this.proposal.choices.map(choice => choice.text);
    }
  }
};
</script>
